import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";

// Componente del men personalizado
const Menu = () => {
  const [active, setActive] = useState("equipo");

  const handleClick = (option: string) => {
    setActive(option);
    Streamlit.setComponentValue(option); // Enviar seleccin a Streamlit
  };

  useEffect(() => {
    Streamlit.setFrameHeight();
  }, []);

  const menuItems = ["equipo", "dataset", "calculador"];

  return (
    <div style={styles.container}>
      {menuItems.map((item) => (
        <div
          key={item}
          onClick={() => handleClick(item)}
          style={{
            ...styles.item,
            ...(active === item ? styles.active : {}),
          }}
        >
          {item.toUpperCase()}
        </div>
      ))}
    </div>
  );
};

// Estilos en lnea
const styles: { [key: string]: React.CSSProperties } = {
  container: {
    position: "sticky",
    top: 0,
    zIndex: 1000,
    backgroundColor: "#ffffff",
    display: "flex",
    justifyContent: "center",
    gap: "3rem",
    padding: "1.2rem 0",
    borderBottom: "2px solid #a51217",
    fontFamily: "'Inter', sans-serif",
  },
  item: {
    fontSize: "16px",
    fontWeight: 600,
    color: "#a51217",
    cursor: "pointer",
    position: "relative",
    textTransform: "uppercase",
    transition: "color 0.2s ease",
  },
  active: {
    borderBottom: "3px solid #a51217",
  },
};

// Conexin con Streamlit
const ConnectedMenu = withStreamlitConnection(Menu);

// Render clsico
ReactDOM.render(<ConnectedMenu />, document.getElementById("root"));




